$(document).ready(function(){
    $(function(){
        setTimeout(function(){
            $('body').removeClass('overflow-hidden');
            $('#loader').hide();
        },5000);
    });
    $('.one').hover(function(){$(this).siblings(".one").toggleClass('show')});
    $('.two').hover(function(){$(this).siblings(".two").toggleClass('show')});
    $('.three').hover(function(){$(this).siblings(".three").toggleClass('show')});
    $('.four').hover(function(){$(this).siblings(".four").toggleClass('show')});
    $('.five').hover(function(){$(this).siblings(".five").toggleClass('show')});
    $('.six').hover(function(){$(this).siblings(".six").toggleClass('show')});
    $('#showLeftMap').click(function(){$(this).siblings(".leftMap").addClass('show')});
    $('#showRightMap').click(function(){$(this).siblings(".rightMap").addClass('show')});
    $('#hideLeftMap').click(function(){$(this).parent().parent().parent().find(".leftMap").removeClass('show')});
    $('#hideRightMap').click(function(){$(this).parent().parent().parent().find(".rightMap").removeClass('show')});
    $('.team-image').hover(function(){$(this).parent().parent().find(".team-inner").toggleClass('show')});
    $('.team-image').hover(function(){$(this).parent().parent().parent().parent().find(".team-list").toggleClass('offAll')});
    $('#testimonials').owlCarousel({
        loop: true,
        center: false,
        items: 2,
        touchDrag: false,
        mouseDrag: false,
        singleItem: false,
        margin: 0,
        autoplay: false,
        dots:false,
        nav: true,
        autoplayTimeout: 8500,
        smartSpeed: 1000,
        responsive: {
            0: {
                items: 1
            },
            768: {
                items: 2
            },
            1170: {
                items: 2
            }
        }
    });
});

$('.carousel').carousel({
    interval: 2000
})
